:root {
  --font-family: "Muller";
  --content-width: 1250px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --primary: #153d65;
  --accent-1: #eaf8f5;
  --accent-2: #428783;
  --button1-bg-color: rgb(33, 33, 35);
  --button1-fg-color: #1e65a4;
  --orange: rgb(222, 152, 22);
  --orange-hover: rgb(154, 110, 28);
  --spacing-small: 10px;
  --spacing-medium: 15px;
  --spacing-large: 20px;
  --spacing-extra-large: 30px;
  --size-small: 16px;
  --size-medium: 18px;
  --size-big: 40px;
  --box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}

