@font-face {
  font-family: "Muller";
  src: url("../fonts/../fonts/MullerRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  background-color: var(--accent-1);
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  background-color: var(--accent-1);
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: var(--spacing-large);
  -webkit-box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
  -moz-box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
  box-shadow: 0px 5px 5px -5px rgba(34, 60, 80, 0.6);
  background-color: var(--accent-1);
  padding: var(--spacing-large);
}

.logo__title {
  color: var(--orange);
  font-weight: 700;
  font-style: italic;
  font-size: var(--size-medium);
}

.nav__list {
  display: flex;
}
.nav__item:not(:last-child) {
  margin-right: var(--spacing-large);
}
.nav__link {
  position: relative;
  color: var(--primary);
  font-size: var(--size-small);
  transition: color 0.4s;
}
@media (min-width: 1025px) {
  .nav__link:hover {
    color: var(--orange);
  }
}
.nav__link:focus {
  color: var(--orange);
}
.nav__link::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px;
  transform: translateX(-50%) scaleX(0);
  transform-origin: 50% 50%;
  width: 100%;
  height: 2px;
  background-color: vaR(--orange);
  transition: transform 0.4s;
}
@media (min-width: 1025px) {
  .nav__link:hover::after {
    transform: translateX(-50%) scaleX(1);
  }
}
.nav__link:focus::after {
  transform: translateX(-50%) scaleX(1);
}
.burger {
  --burger-width: 30px;
  --burger-height: 30px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

@media (max-width: 1024px) {
  .burger {
    display: block;
  }
  .nav {
    padding: 100px 0 50px 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    overflow: auto;
    background-color: var(--accent-1);
    transform: translateY(-100%);
    transition: transform 0.3s ease-in;
  }
  .nav__list {
    flex-direction: column;
    align-items: center;
  }
  .nav__item {
    margin-right: 0;
  }
  .nav__item:not(:last-child) {
    margin-bottom: var(--spacing-large);
    margin-right: 0;
  }
  .nav__link {
    font-size: var(--size-medium);
  }
}
.menu--active {
  transform: translateY(0);
}

@media (max-width: 576px) {
  .logo__title {
    font-size: var(--size-small);
  }
}
.hero {
  height: 100vh;
  padding-top: 200px;
  margin-bottom: 100px;
  position: relative;
}
.hero::after {
  content: "";
  position: absolute;
  top: 0px;
  bottom: 0;
  left: 0;
  z-index: -1;
  opacity: 1;
  height: 100%;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.459)), url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.hero__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__text {
  color: var(--primary);
  max-width: 650px;
}
.hero__title {
  margin-bottom: var(--spacing-large);
  padding-bottom: var(--spacing-large);
  font-size: var(--size-big);
  color: var(--light-color);
  position: relative;
}
.hero__title::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 170px;
  height: 3px;
  background-color: var(--light-color);
}
.hero__subtitle {
  margin: 0;
  margin-bottom: var(--spacing-extra-large);
  color: var(--light-color);
  font-weight: normal;
}
.hero__btn {
  padding: var(--spacing-medium) var(--spacing-extra-large);
  border-radius: 10px;
  background-color: var(--orange);
  color: var(--light-color);
  font-size: var(--size-medium);
  box-shadow: var(--box-shadow);
  transition: all 0.3s;
}
.hero__btn:hover {
  background-color: var(--orange-hover);
}
.hero__benefits-title {
  color: var(--light-color);
  font-size: 30px;
  font-weight: normal;
  padding-left: 40px;
  position: relative;
}
.hero__benefits-title:not(:last-child) {
  margin-bottom: 50px;
}
.hero__benefits-title::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 30px;
  background-color: var(--orange);
  border-radius: 50%;
}
.hero__benefits-title::before {
  content: "";
  position: absolute;
  top: 60%;
  left: 3.5%;
  width: 2px;
  height: 100%;
  background-color: var(--orange);
  padding-bottom: 100px;
}
.hero__benefits-title:last-child::before {
  display: none;
}
.hero__benefits-subtitle {
  color: var(--light-color);
  padding-left: 40px;
  font-weight: normal;
}

.scroll-mouse {
  display: block;
  width: 30px;
  height: 50px;
  border: 2px solid var(--primary);
  border-radius: 50px;
  position: absolute;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -o-transform: translateX(-50%);
  transform: translateX(-50%);
}

.scroll-mouse::after,
.scroll-mouse::before {
  content: "";
  position: absolute;
  top: 20%;
  left: 50%;
  height: 10px;
  width: 10px;
  border: 2px solid var(--primary);
  -webkit-transform: translate(-50%, -100%) rotate(45deg);
  -moz-transform: translate(-50%, -100%) rotate(45deg);
  -ms-transform: translate(-50%, -100%) rotate(45deg);
  -o-transform: translate(-50%, -100%) rotate(45deg);
  transform: translate(-50%, -100%) rotate(45deg);
  border-top: transparent;
  border-left: transparent;
  animation: scrollDown 1.5s infinite;
}

.scroll-mouse::after {
  top: 20%;
  animation-delay: 0.4s;
}

@keyframes scrollDown {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    top: 80%;
  }
}
@media (max-width: 1024px) {
  .hero__container {
    flex-wrap: wrap;
  }
  .hero__text {
    max-width: 100%;
    justify-content: center;
    margin-bottom: 30px;
  }
  .hero__benefits-title:not(:last-child) {
    margin-bottom: 10px;
  }
  .hero__benefits-title::before {
    padding-bottom: 50px;
  }
}
@media (max-width: 576px) {
  .hero {
    padding-top: 100px;
  }
  .hero__container {
    justify-content: center;
  }
  .hero__title {
    font-size: 21px;
  }
  .hero__benefits-title {
    font-size: 17px;
  }
  .hero__benefits-title::after {
    width: 20px;
    height: 20px;
  }
  .hero__benefits-title::before {
    left: 3%;
  }
  .scroll-mouse {
    bottom: 90px;
  }
}
.about {
  padding: var(--spacing-extra-large) 0 50px 0;
  margin-bottom: 100px;
}
.about__inner {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}
.about__title {
  font-size: var(--size-medium);
  color: var(--primary);
  margin-bottom: var(--spacing-small);
}
.about__text {
  color: var(--primary);
  max-width: 600px;
}
.about__text p {
  display: flex;
  margin: 0;
  margin-bottom: var(--spacing-small);
}
.about__icon {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  fill: var(--orange);
  margin-right: var(--spacing-small);
}
.about__img img {
  border-radius: 50px;
  box-shadow: var(--box-shadow);
}

@media (max-width: 1145px) {
  .about__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
  .about__descr {
    margin-bottom: var(--spacing-extra-large);
  }
}
.title {
  display: inline-flex;
  white-space: nowrap;
  margin: 0 50%;
  transform: translateX(-50%);
  font-size: var(--size-big);
  color: var(--primary);
  font-weight: 700;
  margin-bottom: 50px;
  position: relative;
}
.title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--primary);
}

.title-light {
  display: inline-flex;
  white-space: nowrap;
  margin: 0 50%;
  transform: translateX(-50%);
  font-size: var(--size-big);
  color: var(--light-color);
  font-weight: 700;
  margin-bottom: 50px;
  position: relative;
}
.title-light::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: var(--light-color);
}

.benefits {
  padding: 50px 0;
  background-color: rgb(254, 254, 254);
}
.benefits__subtitle {
  font-weight: normal;
  text-align: center;
  color: var(--primary);
  margin-bottom: var(--spacing-large);
}
.benefits__columns {
  display: flex;
}
.benefits__column {
  display: flex;
  flex: 1 1 33.3333%;
  padding: 0 -20px;
}
.benefits__item {
  display: flex;
  flex-direction: column;
  height: 250px;
  margin: 0 10px;
  position: relative;
  background-color: var(--light-color);
  padding: var(--spacing-large);
  border-radius: 15px;
  box-shadow: var(--box-shadow);
  overflow: hidden;
  border-left: 3px solid var(--orange);
}
.benefits__item-title {
  text-align: center;
  color: var(--primary);
  margin-top: var(--spacing-extra-large);
  flex: 1 0 auto;
}
.benefits__item-text {
  color: var(--primary);
  overflow: auto;
}

.benefits-icon {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 100px;
  height: 100px;
  fill: var(--orange);
  opacity: 0.3;
  transform: rotate(45deg);
}

@media (max-width: 1000px) {
  .benefits__columns {
    flex-wrap: wrap;
  }
  .benefits__column {
    flex: 0 1 50%;
    margin-bottom: var(--spacing-extra-large);
  }
}
@media (max-width: 580px) {
  .benefits__column {
    flex: 0 1 100%;
  }
  .benefits__column:last-child {
    margin-bottom: 0;
  }
}